.site-wrap .crumbs {
  border-bottom: 2px solid #A0A0A0;
  margin-bottom: 20px;
}
@media print {
  .site-wrap .crumbs {
    display: none;
  }
}
.site-wrap .crumbs.simple {
  border-bottom: none;
  margin-bottom: 20px;
}
.site-wrap .crumbs.simple .breadcrumb {
  padding-bottom: 0;
}
.site-wrap .crumbs .breadcrumb {
  background: none;
  color: #165D8B;
  font-size: 16px;
  margin: 20px 0;
  padding: 0;
  position: relative;
}
@media (max-width: 767px) {
  .site-wrap .crumbs .breadcrumb {
    max-width: calc(100% - 132px);
  }
}
.site-wrap .crumbs .breadcrumb i {
  font-size: 15px;
  left: 0;
  margin-right: 5px;
  position: absolute;
  top: 7px;
}
.site-wrap .crumbs .breadcrumb span {
  color: #3e3d3c;
}
.site-wrap .crumbs .breadcrumb .text {
  display: inline-block;
  line-height: 22px;
  padding-left: 20px;
}
.site-wrap .crumbs .breadcrumb a {
  color: #9ac53c;
}
.site-wrap .crumbs .breadcrumb a:hover, .site-wrap .crumbs .breadcrumb a:focus {
  color: #6aab43;
}
.site-wrap .crumbs .breadcrumb a.home {
  font-size: 0;
  position: relative;
}
.site-wrap .crumbs .breadcrumb a.home::before {
  color: #3e3d3c;
  content: "\e910";
  font-family: icomoon;
  font-size: 24px;
  line-height: 16px;
  vertical-align: -3px;
  -webkit-transition: color 0.3s linear;
  -moz-transition: color 0.3s linear;
  -ms-transition: color 0.3s linear;
  -o-transition: color 0.3s linear;
  transition: color 0.3s linear;
}
.site-wrap .crumbs .breadcrumb a.home:hover::before, .site-wrap .crumbs .breadcrumb a.home:focus::before {
  color: #9ac53c;
}
.site-wrap .viewall-crumb {
  border-left: 1px solid #d8e6f8;
  display: inline-block;
  font-size: 16px;
  margin: 20px 20px 20px 14px;
  padding: 0 0 0 20px;
}
@media (max-width: 991px) {
  .site-wrap .viewall-crumb {
    display: none;
  }
}